import wPicker from "@/components/w-picker/w-picker.vue"
import axbCheckBox from '../../components/axb-checkbox/axb-checkbox.vue'
export default {
	data() {
		return {
			present: '',
			show1:false,
			begin: '',
			overtime: '',
			behoof:'',
			meeting:'',
			uerInfo:'',
			sartTime: [{ // 开始日期
					name: '小会议室',
					value: '小会议室',
					checked: 0
				}, {
					name: '大会议室',
					value: '大会议室',
					checked: 0
				},
				{
					name: '咖啡厅',
					value: '咖啡厅',
					checked: 0
				}
			],
		}
	},
	components: {
		wPicker,
		axbCheckBox,
	},
	methods: {
		showPicker(type) {
			this.$refs[type].show();
		},
		onConfirm(res, type) {
			this.result = res;
			this.begin = res;

		},

		onConfirm2(res, type) {
			this.result = res;
			this.overtime = res;
		},
		onCancel() {
			console.log('取消选择')
		},

		radioChangeType: function(val) {
			console.log(val) 
			this.meeting=val;
		},
		bindTextAreaBlur: function(e) {
			console.log(e.detail.value)
			this.behoof=e.detail.value;
		},
		
		//订阅消息
		dyxi(){				
			console.log('dyxx');
			wx.requestSubscribeMessage({
			  tmplIds: ['JijPxhl-oR5WSGzfYzep8ZbrBY-gPEnJPur0RSqgbp0'], // 此处可填写多个模板 ID，但低版本微信不兼容只能授权一个
			  success (res) {
			    console.log('已授权接收订阅消息')
			  }
			})
			
			},
		
		submit:function()
		{
			let _this = this;				
			uni.request({
			    url:  _this.$loginUrl + '/api/v1/order', //仅为示例，并非真实接口地址。
			    data: {
			        begin: _this.begin,
					overtime:_this.overtime,
					behoof:_this.behoof,
					meeting:_this.meeting,
					uerInfo:_this.uerInfo,
					session_key: uni.getStorageSync('storage_key')
			    },
				method: 'POST',
			    header: {
			        'custom-header': 'hello' //自定义请求头信息
			    },
			    success: (res) => {
			        console.log(res.data);		
					if(res.data.code==400){
						uni.showToast({
						   title: res.data.message,							  
						   duration: 2000,
						   icon:'none',
						});
					}else{
						uni.showToast({
						   title: '预约成功',							  
						   duration: 2000,
						   icon:'none',
						});
						uni.redirectTo({
						   	url: '/pages/forward/forward'
						});
					}

			    },
				fail:(res)=>{
					   console.log(res.data);
				}
			});
		},
		onLoad() {
			// this.present = this.date()
			this.uerInfo=getApp().globalData.userInfo;
			if(this.uerInfo!=null){
				this.login= true;
			}
			this.show1= true;
		},

		date() {
			Date.prototype.Format = function(fmt) { // author: meizz
				var o = {
					"M+": this.getMonth() + 1, // 月份
					"d+": this.getDate(), // 日
					"h+": this.getHours(), // 小时
					"m+": this.getMinutes(), // 分
					"s+": this.getSeconds(), // 秒
					"q+": Math.floor((this.getMonth() + 3) / 3), // 季度
					"S": this.getMilliseconds() // 毫秒
				};
				if (/(y+)/.test(fmt))
					fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
				for (var k in o)
					if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" +
						o[k]).substr(("" + o[k]).length)));
				return fmt;
			}
			return new Date().Format("yyyy-MM-dd hh:mm:ss");
		}

	}
}
